    import {reqGiftList} from '@/network/teamReq'
    export default {
        name: "GiftContent",
        data() {
            return {
                giftList:{},
                selectedIndex: -1,
				baseUrl: this.$baseUrl
            }
        },
		props:{
			tit: Number
		},
        
		watch:{
			tit(newValue){
				if(newValue){
					this.getGiftList()
				}
			}
		},
        methods:{
            getGiftList(){
                reqGiftList(this.tit).then(res=>{
                    this.giftList = res.data
                })
            }
        }
    }
